
import angular from 'angular';//1.6.9

import accordion from 'angular-ui-bootstrap/src/accordion';
import datepicker from 'angular-ui-bootstrap/src/datepicker'; 
import modal from 'angular-ui-bootstrap/src/modal/index-nocss.js'; 
import carousel from 'angular-ui-bootstrap/src/carousel/index-nocss.js';

import datepickerPopup from 'angular-ui-bootstrap/src/datepickerPopup';

import {postData} from './js/myFetch.js';
import {WsMainController} from './myCtrl.js'; 

const myApp = angular.module("myAppuploads", [require('angular-touch'),require('angular-animate'),carousel,modal,datepickerPopup]);

myApp.config(['$locationProvider','$interpolateProvider', function($locationProvider,$interpolateProvider){
     $locationProvider.html5Mode({
              enabled:true
    });
    $interpolateProvider.startSymbol('{>');
    $interpolateProvider.endSymbol('<}'); 
}]);

window.postData=postData;

//communicatorService
myApp.controller("WsController", ['$scope','$compile','$timeout','$controller','$uibModal',WsMainController]);
